<script setup>
import router from '@/router'
import { useAisdeMenu } from '@/stores/AsideMenu'
const menu = useAisdeMenu().totalMenu

const checkActive = (item) => {
  useAisdeMenu().checkActive(item)
  router.push({ name: item.name })
}
</script>

<template>
  <div class="container">
    <div
      class="asideItem"
      v-for="(item, index) in menu"
      v-bind:class="{ active: item.isActive }"
      :key="index"
      @click="checkActive(item)"
    >
      {{ item.name }}
    </div>
  </div>
</template>

<style scoped>
.container {
  padding-top: 2px;
  /* height: 100%; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.container .asideItem {
  position: relative;
  color: #a7a7a7;
  height: 48px;
  width: 180px;
  padding-left: 16px;
  padding-right: 16px;
  text-align: center;
  line-height: 48px;
  cursor: pointer;
  border-radius: 8px;
  margin-top: 20px;
}
.container .asideItem:hover {
  background-color: #dde5f5;
}

.container .asideItem.active::before {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 50%;
  width: 20px;
  height: 2px;
  margin-left: -10px;
  border-radius: 66px;
  opacity: 1;
  background: linear-gradient(270deg, #816ff1 20%, #6663f7 59%, #7583ff 99%);
  z-index: 9;
}
</style>
