import { ref } from 'vue'
import { defineStore } from 'pinia'

export const useAisdeMenu = defineStore(
  'totalMenuList',
  () => {
    const totalMenu = ref([
      { name: 'PartyEx', isActive: true },
      { name: 'Scribble', isActive: false },
      { name: 'AssistTool', isActive: false }
    ])
    function checkActive(item) {
      if (item.isActive) {
        return
      }
      totalMenu.value.forEach((t) => (t.isActive = false))
      item.isActive = true
    }
    return { totalMenu, checkActive }
  },
  {
    persist: true
  }
)
